<template>
    <div class="box">
        <div class="box-body">
            <div class="col">
                <h2>Liên Kết</h2>
                <div v-for="item in about.filter(e=>e.id_group==1)" :key="item.id">
                    <div class="footer-item">
                        <div v-html="item.name"></div>
                        <div class="tools">
                            <router-link :to="{name: 'route.admin.about.editfooter',params:{id:item.id}}" class="btn btn-success">Sửa</router-link>
                            <a v-on:click="del(item.id)" class="btn btn-danger">Xóa</a>
                        </div>
                    </div>
                </div>
                <div class="input-group input-group-sm">
                    <input v-on:keyup.enter="add(1)" v-model="post[1]" type="text" class="form-control">
                    <span class="input-group-btn">
                        <button  v-on:click="add(1)"  type="button" class="btn btn-success btn-flat">Thêm</button>
                    </span>
                </div>
            </div>
            <div class="col">
                <h2>Đăng Tin lên Tizi</h2>
                <div v-for="item in about.filter(e=>e.id_group==2)" :key="item.id" v-html="item.name"></div>
                <div class="input-group input-group-sm">
                    <input v-model="post[2]" type="text" class="form-control">
                    <span class="input-group-btn">
                        <button v-on:click="add(2)" type="button" class="btn btn-success btn-flat">Thêm</button>
                    </span>
                </div>
            </div>
            <div class="col">
                <h2>Dành Cho Người Mua</h2>
                <div v-for="item in about.filter(e=>e.id_group==3)" :key="item.id" v-html="item.name"></div>
                <div class="input-group input-group-sm">
                    <input v-model="post[3]" type="text" class="form-control">
                    <span class="input-group-btn">
                        <button v-on:click="add(3)" type="button" class="btn btn-success btn-flat">Thêm</button>
                    </span>
                </div>
            </div>
            <div class="col">
                <h2>Về Tizi</h2>
                <div v-for="item in about.filter(e=>e.id_group==4)" :key="item.id" v-html="item.name"></div>
                <div class="input-group input-group-sm">
                    <input v-model="post[4]" type="text" class="form-control">
                    <span class="input-group-btn">
                        <button v-on:click="add(4)" type="button" class="btn btn-success btn-flat">Thêm</button>
                    </span>
                </div>
            </div>
            <div class="col">
                <h2>Chứng Nhận</h2>
                <div v-for="item in about.filter(e=>e.id_group==5)" :key="item.id" v-html="item.name"></div>
                <div class="input-group input-group-sm">
                    <input v-model="post[5]" type="text" class="form-control">
                    <span class="input-group-btn">
                        <button v-on:click="add(5)" type="button" class="btn btn-success btn-flat">Thêm</button>
                    </span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import slug from "slug";

export default {
    metaInfo: {
        title: "Chỉnh sửa footer"
    },
    data() {
        return {
            api: this.$store.state.domainApi,
            about: [],
            post:{
                1:null,
                2:null,
                3:null,
                4:null,
                5:null
            }
        }
    },
    methods: {
        getData(){
            axios.get(this.api + "/admin/about-footer",{
                headers:{
                    Authorization: `Bearer ${localStorage.token}`
                }
            })
            .then(response=>{
                console.log(response);
                this.about = response.data;
            });
        },
        add(id_group){
            if(this.post[id_group]){
                let data = {
                    id_group: id_group,
                    name: this.post[id_group],
                    url: slug(this.post[id_group])
                }
                axios.post(this.api + "/admin/about-footer", data ,{
                    headers:{
                        Authorization: `Bearer ${localStorage.token}`
                    }
                })
                .then(response=>{
                    if(response.data.success){
                        this.flash(response.data.message, "success", {timeout:3000});
                        this.getData();
                        this.post[id_group]=null;
                    }
                    else this.flash(response.data.message, "error", {timeout:3000});
                });
            }
        },
        del(id){
            this.$swal.fire({
                title: 'Bạn có muốn xóa không',
                text: "",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Không !',
                confirmButtonText: 'Có !'
            }).then((result) => {
                if (result.value) {
                    axios.delete(`${this.api}/admin/about-footer`,{
                        headers: {
                            Authorization: `Bearer ${localStorage.token}`
                        },
                        params:{
                            id: id
                        }
                    })
                    .then(response => {
                        if(response.data.success===1){
                            this.getData();
                            this.flash(response.data.message, 'success', {
                                timeout: 3000
                            });
                        }else{
                            this.flash(response.data.message, 'error', {
                                timeout: 3000
                            });
                        }
                    });
                }
            });
        }
    },
    mounted(){
        this.getData();
    }
}
</script>

